import React from 'react'
import './WeatherList.scss'
import TodayWeather from '../TodayWeather/TodayWeather'
import NextDays from '../NextDays/NextDays'

export default function (props) {
    const { weatherToday } = props;
    const { nextDays } = props;
    nextDays.splice(0, 1)
    return (
        <section className="weather-list">
            {weatherToday.weather && weatherToday.weather.length > 3 && <>
                <div className="title">Hourly Weather</div>
                <div className="today-list" >
                    {weatherToday.weather.splice(6) && weatherToday.weather.map(weather => {
                        return (
                            <TodayWeather key={weather.dt} weather={weather} />
                        )
                    })}
                </div>
            </>}
            <div className="title">Next Days</div>
            <section className="daily-list">
                {<>
                    {nextDays && nextDays.map(weather => {
                        return (
                            <NextDays key={weather.dt} weather={weather} />
                        )
                    })}
                </>}
            </section>
        </section>
    )

}


