import axios from 'axios'
const WEATHER_API = "5b358150ea9a5438e8cf661369d8d9e8";
const BASE_URL = "https://api.openweathermap.org/data/2.5/forecast?"



async function query(lat = "32.0792765", lng = "34.7915845") {

    try {
        const res = await axios.get(`${BASE_URL}lat=${lat}&lon=${lng}&units=metric&appid=${WEATHER_API}`);
        const today = await weatherToday(res.data)
        const next = await nextDaysWeather(res.data);
        const weathers = { today, next }
        return weathers
    }
    catch (err) {
        if (err.response.status === 401) {
        }
    }
}

function timestampToString(timestamp) {
    const date = new Date(timestamp * 1000).toLocaleString('en-us',
        { weekday: 'long', day: 'numeric', month: 'long', })
    return date
}

function shortDate(timestamp) {
    let date = new Date(timestamp * 1000);
    let temp = date;
    temp = temp.toDateString();
    const day = temp.substr(0, 3);
    date = date.toISOString().substr(5, 5);
    date = date.replace('-', '/')
    const shortDate = {
        date,
        day
    }
    return shortDate
}

function timestampToDate(timestamp) {
    const date = new Date(timestamp * 1000).toDateString()
    return date;
}

function convertToHour(date) {
    date = new Date(date)
    const timeStr = date.toLocaleTimeString()
    const splitStr = timeStr.split(':');
    const time = splitStr[0] + ':' + splitStr[1] + ' ' + timeStr.substring(8, 11)
    return time;
}

function weatherToday(results) {
    let dateToday = results.list[0].dt;
    let sunrise = new Date(results.city.sunrise * 1000).toTimeString();
    let sunset = new Date(results.city.sunset * 1000).toTimeString();
    sunrise = sunrise.substr(0, 5)
    sunset = sunset.substr(0, 5)
    dateToday = timestampToDate(dateToday)
    const filterWeather = results.list.filter(weather => {
        return timestampToDate(weather.dt) === dateToday;
    })
    const todaysWeather = {
        weather: filterWeather,
        sunrise,
        sunset,
        city: results.city.name,
        country: results.city.country,
        date: timestampToString(results.list[0].dt)
    }
    return todaysWeather
}

function nextDaysWeather(results) {
    const seen = new Set();
    let weather = results.list;
    const filteredWeather = weather.filter(el => {
        const duplicate = seen.has(timestampToDate(el.dt));
        seen.add(timestampToDate(el.dt));
        return !duplicate;
    });
    return filteredWeather
}

export default {
    query,
    convertToHour,
    shortDate
}