import React from 'react'
import './MainWeather.scss'

export default function (props) {
    const { weather } = props;
    const mainWeather = weather.weather
   
    return (
        <section className="main-weather">
            {weather && mainWeather && <>
                <div className="date-location">
                    <h1 >{weather.city},{weather.country}</h1>
                    <span >{weather.date}</span>
                </div>
                <div className="weather-details">
                    <div className=" divider first-panel">
                        <div className="flex flex-row align-center space-around">
                            <div className="icon-temp">
                                <img className="main-icon" src={`http://openweathermap.org/img/wn/${mainWeather[0].weather[0].icon}@2x.png`} />
                            </div>
                            <div className="degree-summary flex  flex-col flex-center align-center">
                                <div>{mainWeather[0].main.temp.toFixed(0)}&#176;</div>
                                <div>{mainWeather[0].weather[0].main}</div>
                            </div>
                        </div>
                    </div>
                    <div className="second-panel flex flex-col flex-center">
                        <div className="temp-stat">
                            <div className="stat min">
                                <div className="stat-val">{mainWeather[0].main.temp_min.toFixed(0)}&#176;</div>
                                <div className="stat-label">Min</div>
                            </div>
                            <div className="stat rain">
                                <div className="stat-val">{mainWeather[0].main.feels_like.toFixed(0)}&#176;</div>
                                <div className="stat-label">Feels</div>
                            </div>
                            <div className="stat sunrise">
                                <div className="stat-val">{weather.sunrise}</div>
                                <div className="stat-label">Sunrise</div>
                            </div>
                            <div className="stat max">
                                <div className="stat-val">{mainWeather[0].main.temp_max.toFixed(0)}&#176;</div>
                                <div className="stat-label">Max</div>
                            </div>
                            <div className="stat wind">
                                <div className="stat-val">{mainWeather[0].main.humidity.toFixed(0)}%</div>
                                <div className="stat-label">Humidity</div>
                            </div>
                            <div className="stat sunset">
                                <div className="stat-val">{weather.sunset}</div>
                                <div className="stat-label">Sunset</div>
                            </div>
                        </div>
                    </div>
                </div>
            </>}
        </section>

    )
}