import React from 'react'
import './TodayWeather.scss'
import weatherService from '../../services/WeatherService'

export default function (props) {
    const { weather } = props;
    return (
        <div className="today-details flex flex-col align-center">
            <div className="today-detail">{weatherService.convertToHour(weather.dt_txt)}</div>
            <img src={`http://openweathermap.org/img/wn/${weather.weather[0].icon}@2x.png`} />
            <div className="today-detail">{weather.main.temp.toFixed(0)}&#176;</div>
        </div>
    )
}