
const INITIAL_STATE = {
    weatherToday: [],
    nextDays:[]
}


export default function weathertReducer(state = INITIAL_STATE, action) {
    switch (action.type) {
        case 'SET_WEATHER':
            return {
                ...state,
                weatherToday: action.weather.today,
                nextDays:action.weather.next
            }
        default:
            return state
    }
}

