import React from 'react'
import { connect } from 'react-redux'
import { loadWeather } from '../../modules/weather/actions'

import './HomePage.scss';
import MainWeather from '../../cmps/MainWeather/MainWeather'
import WeatherList from '../../cmps/WeatherList/WeatherList'
import WeatherSearch from '../../cmps/Search/WeatherSearch'
import geoService from '../../services/GeoService'

class HomePage extends React.Component {
    state = {
      
    }

    async componentDidMount() {
        await this.props.loadWeather()
    }

    setSearch = async (value) => {
        const address = await geoService.query(value);
        const {lat}=address;
        const {lng}=address;
        await this.props.loadWeather(lat,lng)
    }

    render() {
        const { weatherToday } = this.props.weather;
        const { nextDays } = this.props.weather;
        return (
            <>
                <WeatherSearch search={this.setSearch} />
                {weatherToday && nextDays && <>
                    <MainWeather weather={weatherToday} />
                    <WeatherList nextDays={nextDays} weatherToday={weatherToday} />
                </>}
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        weather: state.weather,
    }
}

const mapDispatchToProps = {
    loadWeather,
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(HomePage)