import React from 'react'
import './WeatherSearch.scss'

export default function (props) {
    let searchTerm = '';
    const handleFilter = (value) => {
        searchTerm = value
    }
    const keyPress = (e) => {
        if (searchTerm !== '' && e.keyCode == 13) {
            props.search(searchTerm)
        }
    }
    return (
        <section className="search" >
            <input className="input-search" placeholder="Search" onKeyDown={keyPress} type="text" onChange={(ev) => handleFilter(ev.target.value)} />
        </section>
    )
}

