import React from 'react'
import './NextDays.scss'
import weatherService from '../../services/WeatherService'

export default function (props) {
    const { weather } = props;
    return (
        <div className="next-details flex space-around flex-row">
            <div className="items">
                <div className="item-val">{weatherService.shortDate(weather.dt).day}</div>
                <div className="item-label">{weatherService.shortDate(weather.dt).date}</div>
            </div>
            <div className="items">
            <img  src={`http://openweathermap.org/img/wn/${weather.weather[0].icon}@2x.png`} />

            </div>
            <div className="items">
                <div className="item-val">{weather.main.temp_min.toFixed(0)}&#176;</div>
                <div className="item-label">Min</div>
            </div>
            <div className="items">
                <div className="item-val">{weather.main.temp_max.toFixed(0)}&#176;</div>
                <div className="item-label">Max</div>
            </div>
            <div className="items">
                <div className="item-val">{weather.main.feels_like.toFixed(0)}&#176;</div>
                <div className="item-label">Feels</div>
            </div>
            <div className="items">
                <div className="item-val">{weather.main.humidity.toFixed(0)}%</div>
                <div className="item-label">Humidity</div>
            </div>

        </div>
    )

}